n: null,\n  hoverMonthly: false,\n  hoverYearly: false,\n  hoverForever: false,\n  hoverMonthlyFull: false,\n  hoverYearlyFull: false,\n  hoverForeverFull: false,\n})\n\nfunction payNow(subscription) {\n  if (subscription === 'monthly') {\n    shopStore.upgradeMonthly()\n    Inertia.get('/shop/subscribe')\n  }\n  if (subscription === 'yearly') {\n    shopStore.upgradeYearly()\n    Inertia.get('/shop/subscribe')\n  }\n  if (subscription === 'forever') {\n    shopStore.upgradeForever()\n    Inertia.get('/shop/subscribe')\n  }\n}\n\n</script>\n\n<style scoped>\n\n.upgradeIcon {\n  font-size: xxx-large;\n}\n\n.productName {\n  font-size: large;\n  font-weight: bold;\n  padding-top: 1rem;\n}\n\n.price {\n  font-size: xxx-large;\n  color: dodgerblue;\n  vertical-align: top;\n}\n\n.active {\n  background-color: #0a59da;\n}\n\n.activeIcon {\n  color: #0a59da;\n}\n\n</style>\n\n"],"sourceRoot":""}]);
